.demo body:after, .bd-example body:after {
  content: "xs,sm,md,lg,xl,xxl";
  display: none;
}

.demo body:before, .bd-example body:before {
  display: none;
  content: "xs";
}

@media (min-width: 576px) {
  .demo body:before, .bd-example body:before {
    content: "sm";
  }
}

@media (min-width: 768px) {
  .demo body:before, .bd-example body:before {
    content: "md";
  }
}

@media (min-width: 992px) {
  .demo body:before, .bd-example body:before {
    content: "lg";
  }
}

@media (min-width: 1200px) {
  .demo body:before, .bd-example body:before {
    content: "xl";
  }
}

@media (min-width: 1440px) {
  .demo body:before, .bd-example body:before {
    content: "xxl";
  }
}

